import * as React from 'react'
import makeStyles from '@material-ui/styles/makeStyles'
import EmailIcon from '@material-ui/icons/Email'
import PhoneIcon from '@material-ui/icons/PhoneIphone'
import FaceIcon from '@material-ui/icons/Face'
import LocationIcon from '@material-ui/icons/LocationOn'
import { sharedStyles } from '../../util'
import { rahel } from './shared'
import { Theme } from '@material-ui/core'

const useStyles = makeStyles((theme: Theme) => ({
    ...sharedStyles(theme),
    email: {
        transform: 'translate(0,-2px)',
        display: 'inline-block'
    }
}))

type Props = Readonly<{
    hideName?: boolean
    hideLocation?: boolean
}>

const Contact = ({ hideName = false, hideLocation = false }: Props) => {
    const classes = useStyles()
    const name = hideName ? (
        <span />
    ) : (
        <span>
            <FaceIcon className={classes.icon} />
            {rahel}
            <br />
        </span>
    )
    const location = hideLocation ? (
        <span />
    ) : (
        <span>
            <LocationIcon className={classes.icon} />
            Basel area (or remote)
        </span>
    )
    return (
        <span>
            {name}
            <span className={classes.email}>
                <EmailIcon className={classes.icon} />
                <a href="mailto:rahel@code-counseling.ch">rahel@code-counseling.ch</a>
            </span>
            <br />
            <PhoneIcon className={classes.icon} />
            +41 79 744 5319
            <br />
            {location}
        </span>
    )
}

export default Contact
