import * as React from 'react'
import { ReactNode, useState } from 'react'

import AppBar from '@material-ui/core/AppBar/AppBar'
import { Tab, Tabs, Theme } from '@material-ui/core'
import CssBaseline from '@material-ui/core/CssBaseline/CssBaseline'
import { makeStyles, ThemeProvider } from '@material-ui/styles'
import HomeIcon from '@material-ui/icons/Home'
import logo from '../../page/components/logo.svg'
import { LinkTo } from '../../util'

const useStyles = makeStyles((theme: Theme) => ({
    tabs: {
        paddingLeft: '10vw'
    },
    content: {
        display: 'grid',
        minHeight: '100vh',
        paddingTop: 64 + theme.spacing.unit + 'px', // account for toolbar height
        paddingBottom: '3vw',
        marginLeft: '10vw',
        marginRight: '10vw',
        position: 'relative'
    },
    logo: {
        display: 'none', // show selectively, on individual pages
        [theme.breakpoints.down('sm')]: {
            marginTop: '10px',
            width: '150px',
            height: '150px',
            position: 'relative',
            display: 'grid',
            justifySelf: 'end'
        },
        [theme.breakpoints.down('xs')]: {
            justifySelf: 'center'
        }
    }
}))

export type Props = Readonly<{
    theme: Theme
}> &
    ThemedAppProps

const MainPage = ({ theme, children }: Props) => {
    return (
        // This is the only place we need to access the theme via our own model
        // From here on, it is safe and convenient to use the `useTheme` hook
        <ThemeProvider theme={theme}>
            <ThemedApp children={children} />
        </ThemeProvider>
    )
}

type ThemedAppProps = Readonly<{
    children?: ReactNode
}>

const ThemedApp = ({ children }: ThemedAppProps) => {
    const classes = useStyles()
    const initialTabIndex = (href: string) => {
        if (href.endsWith('you')) {
            return 1
        } else if (href.endsWith('me') || href.endsWith('cv')) {
            return 2
        } else if (href.endsWith('how')) {
            return 3
        } else {
            return 0
        }
    }
    const [value, setValue] = useState(initialTabIndex(window.location.href))

    const onTabChange = (event: React.ChangeEvent<{}>, v: any) => {
        setValue(v)
    }

    return (
        <div>
            <CssBaseline />
            <AppBar>
                <Tabs value={value} onChange={onTabChange} indicatorColor="secondary" className={classes.tabs}>
                    <Tab icon={<HomeIcon />} component={LinkTo('/')} />
                    <Tab label="You" component={LinkTo('/you')} />
                    <Tab label="Me" component={LinkTo('/me')} />
                    <Tab label="How" component={LinkTo('/how')} />
                </Tabs>
            </AppBar>
            <div className={classes.content}>
                {children}
                <img src={logo} className={classes.logo} alt="Code Counseling" />
            </div>
        </div>
    )
}

export default MainPage
