import * as React from 'react'
import { Redirect, Route, Switch } from 'react-router'
import HomePage from './HomePage'
import YouPage from './YouPage'
import MePage from './MePage'
import HowPage from './HowPage'
import CvPage from './CvPage'

const Routes = () => {
    return (
        <Switch>
            <Route exact={true} path="/" component={HomePage} />
            <Route exact={true} path="/you" component={YouPage} />
            <Route exact={true} path="/me" component={MePage} />
            <Route exact={true} path="/me/cv" component={CvPage} />
            <Route exact={true} path="/how" component={HowPage} />
            // Fallback for all invalid paths
            <Redirect to="/" />
        </Switch>
    )
}

export default Routes
